import React from 'react';
import withFormPage from './withFormPage';
import formsService from '../../services/forms.services'
import { withAuthConsumer } from '../../contexts/AuthStore';
import useStyles from '../../styles/forms';
import {useForm} from '../../hooks';
import Form from './form';
import Typography from '@material-ui/core/Typography';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
const Strategies = ({user}) => {
  const {data:{id}} = user;
  const classes = useStyles();
  const update = formsService.updateStr(1)(id);
  const get = formsService.readStr(1)(id);
  const { ...fields } = useForm({update, get});
  const options = [
    '1 es nunca o casi nunca',
    '2 generalmente no lo hago (menos de la mitad de las veces),',
    '3 a veces (ms o menos), ',
    '4 a menudo (ms de la mitad de las veces) ',
    '5 siempre o casi siempre',
  ]

  return (
  <React.Fragment>
    <Typography variant="body1" gutterBottom >
      {'Este cuestionario ha sido diseado para estudiantes de ingls como lengua extranjera que participan en conversaciones por pareja. Por favor lee cada enunciado y selecciona la respuesta marcando con una X la casilla (1,2,3,4,5) que ms se acerque a tu realidad.'}
    </Typography>
    <List component="nav" aria-label="secondary mailbox folders" style={flexContainer}>
      {options.map((item, i)=>(
        <ListItem key={i}>
          <ListItemText primary={item} />
        </ListItem>
      ))}
    </List>
    <Typography variant="body1" gutterBottom >
      {'Las respuestas en cada enunciado te describen slo a ti. No hay respuestas correctas ni incorrectas. Tmate el tiempo necesario para resolver cada tem. Usualmente deberas tardar entre 15-20 minutos para rellenarlo. Si tienes alguna pregunta consulta con el investigador.'}
    </Typography>
    <Typography variant="h6">
      {'Durante una conversacin por parejas yo'}
    </Typography>
    <Form {...fields} classes={classes} />
  </React.Fragment>
  )
}

const flexContainer = {
  display: 'flex',
  alignItems: 'baseline',
  padding: 0,
};


export default withAuthConsumer(withFormPage(Strategies));